body {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #333;
    margin: 0;
    padding: 0;
}

a {
    color: #08c;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #222;
    font-weight: 100;
    margin: 1em 0;
}

// 编辑框主体样式
.editor-main-c {
    display: flex;
    overflow: hidden;
    .common-container {
        width: 50%;
    }
    .editor-container {
        border-bottom: 1px solid #ddd;
    }
    .preview-container {
        overflow-y: scroll;
        border: 1px solid #ddd;
        border-top: none;
        .preview-wrapper {
            background-color: inherit;
            padding: 20px;
            *:last-child {
                margin-bottom: 0;
            }
            *:first-child {
                margin-top: 0;
            }
        }
    }
}

.pop-over {
    text-align: right;
    line-height: 0.4rem;
}

.ReactCodeMirror {
    height: 100%;
    .CodeMirror {
        height: 100%;
        box-shadow: none;
        background-color: inherit;
    }
}